* {
  margin: 0;
  padding: 0;
}

body,
html {
  box-sizing: border-box;
  background-color: #1c1c25;
  font-family: "Ubuntu", sans-serif;
  font-weight: 700;
}

nav {
  background-color: #272733;
  border-bottom: 1px solid black;
  height: 7vh;
  line-height: 7vh;
}

.nav-links {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin-left: 25em;
}

.logo {
  margin-right: 5em;
}

.logo-label {
  display: flex;
  justify-content: flex-end;
  width: 20em;
  margin-right: 5em;
}

.news {
  width: 5em;
}

.sports-label {
  display: flex;
  justify-content: space-around;
  width: 20em;
}

li {
  color: white;
}

a {
  text-decoration: none;
}

h1 {
  display: flex;
  justify-content: center;
  color: white;
}

img {
  object-fit: cover;
}

.home-container {
  width: 100%;
  height: auto;
  position: absolute;
  display: flex;
  justify-content: center;
}

.box-container {
  margin-top: 5em;
  height: auto;
  width: 67em;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3em;
  align-items: center;
}

.main-news {
  display: grid;
  width: auto;
  height: auto;
  border: solid black 1px;
  border-radius: 5px;
  overflow: hidden;
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 3;
  cursor: pointer;
  color: white;
}

.main-news-image {
  width: 100%;
  height: 30em;
}

.main-news-description {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: black;
}

.main-news-title {
  font-size: 1.5em;
  padding-top: 0.5em;
  padding-left: 1em;
}

.main-news-author {
  padding-top: 1em;
  padding-left: 1em;
  padding-bottom: 1em;
}

.news-card {
  display: flex;
  flex-direction: column;
  width: 20em;
  height: 20em;
  border: solid black 1px;
  border-radius: 5px;
  overflow: hidden;
  cursor: pointer;
  background-color: black;
  color: white;
}

.news-card-container {
  height: 20em;
}

.news-image-div {
  height: 70%;
}

.news-image {
  width: 100%;
  height: 100%;
}

.news-card-description {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 5.5em;
  background-color: black;
}

.news-card-title {
  padding-left: 0.5em;
  padding-top: 0.5em;
}

.news-card-author {
  font-size: 0.85em;
  padding-left: 0.5em;
}
